.form__login {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-image: url("https://img.freepik.com/premium-photo/abstract-digital-communication_34629-318.jpg");
}
.wrapper {
  width: 420px;
  background-color: rgba(255, 255, 255, 0.11);
  color: #fff;
}
.wrapper h1 {
  font-size: 36px;
  text-align: center;
}
.wrapper .input__box {
  width: 100%;
  height: 50px;
  margin: 30px 0;
}
.input__box input {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  border-radius: 40px;
  font-size: 16px;
  padding: 20px 45px 20px 20px;
}

.wrapper .btn {
  width: 100%;
  height: 45px;
  background: rgba(240, 237, 236, 0.705);
  border: none;
  outline: none;
  border-radius: 40px;
  cursor: pointer;
  font-size: 16px;
  color: #333;
  font-weight: 600;
}
a {
  color: #0056b3;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Error Styles */
.error {
  color: white;
  font-size: 14px;
  margin-top: 10px;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .login-container {
    width: 90%;
  }
}

@media (max-width: 576px) {
  .login-title {
    font-size: 24px;
  }

  .input-container input {
    font-size: 14px;
  }

  .btn {
    font-size: 14px;
  }
}
